@charset "utf-8";
/* 禁用iPhone中Safari的字号自动调整  */
html {
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  /* 解决IOS默认滑动很卡的情况 */
  -webkit-overflow-scrolling : touch;
  font-size: 20px; /* no */
}
/* 禁止缩放表单 */
input[type=“submit”], input[type=“reset”], input[type=“button”], input {
  resize: none;
  border: none;
}
* {
  -webkit-overflow-scrolling: touch;
}
/* 取消链接高亮 */
body, div, ul, li, ol, h1, h2, h3, h4, h5, h6, input, textarea, select, p, dl, dt, dd, a, img, button, form, table, th, tr, td, tbody, article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
/* 设置HTML5元素为块 */
article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  display: block;
}
/* /* 图片自适应 / */
img {
  width: 100%;
  height: auto;
  width: auto\9;
  display: block;
  -ms-interpolation-mode: bicubic;
}
/* /* 初始化 / */
body, div, ul, li, ol, h1, h2, h3, h4, h5, h6, input, textarea, select, span, p, dl, dt, dd, a, img, button, form, table, th, tr, td, tbody, article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section {
  margin: 0;
  padding: 0;
}
body {
  font: 12px/1.5 "Arial","Microsoft YaHei","\9ED1\4F53","\5B8B\4F53",sans-serif;
  color: #555;
  background-color: #fff;
}
em, i {
  font-style: normal;
}
ul,li{
  list-style-type: none;
}
strong {
  font-weight: normal;
}
.clearfix:after {
  content: "";
  display: block;
  visibility: hidden;
  height: 0;
  clear: both;
}
.clearfix {
  zoom: 1;
}
a {
  text-decoration: none;
  color: #969696;
  font-family: tahoma, arial, "PingFang SC", "Microsoft YaHei", Tahoma, Arial, sans-serif, \5b8b\4f53;
}
a:hover {
